import React, { Suspense, lazy } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styled from "styled-components";
import { Route, Routes } from "react-router-dom";
import Header from "./components/Header";
import { EPath } from "./app.constant";

const Home = lazy(() => import("./pages/Home"));
const List = lazy(() => import("./pages/List"));
const Detail = lazy(() => import("./pages/Detail"));
const View = lazy(() => import("./pages/View"));

function App() {
	return (
		<>
			<Header />
			<Suspense fallback={<div>Loading...</div>}>
				<Routes>
					<Route path={"/"} element={<Home />} />
					<Route path={`/${EPath.HOME}`} element={<Home />} />
					<Route path={`/${EPath.VIEW}`} element={<View />} />
					<Route path={`/${EPath.GOODS}/:id`} element={<Detail />} />
					<Route path={`/${EPath.LIST}`} element={<List />} />
					<Route path={`/${EPath.LAVERE23}`} element={<Home />} />
					<Route path={`/${EPath.LAVERE23}/${EPath.HOME}`} element={<Home />} />
					<Route path={`/${EPath.LAVERE23}/${EPath.VIEW}`} element={<View />} />
					<Route
						path={`/${EPath.LAVERE23}/${EPath.GOODS}/:id`}
						element={<Detail />}
					/>
					<Route path={`/${EPath.LAVERE23}/${EPath.LIST}`} element={<List />} />
				</Routes>
			</Suspense>
		</>
	);
}

export default App;
